.card{
  width: 16rem;
  min-height: 15rem;
  margin: 1rem 1rem;
  border: 1px solid rgb(228, 228, 228);
  border-radius: 5px;
  background:  rgb(255, 255, 255);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-rows: auto 2fr auto;
  grid-template-areas:
    "header"
    "card-content"
    "action"
  ;
}

.card__header{
  grid-area: header;
  width: calc(100% - 2rem);
  padding: 0 1rem;
  border-bottom: 1px solid rgb(228, 228, 228);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.card__content{
  grid-area: card-content;
  padding: 0.5rem 1rem;
}

.card__action{
  grid-area: action;
  float: bottom;
}

@media (min-width: 1024px) {
  .card{
    width: 22rem;
  }
}

@media (min-width: 1440px) {
  .card{
    width: 24rem;
  }
}
:root{
  --primaryBlue: #2196F3;
  --primaryActBlue: #1d7cca;
  --info: #00BCD4;
  --infoAct: #0098ac;
  --danger: #F44336;
  --dangerAct: #c5382e;
  --warn: #FFEB3B;
  --warnAct: #dbc605;
  --success: #4CAF50;
  --successAct: #3c8b3f;
}


.btn{
  z-index: 1;
  border: 0 solid transparent;
  border-radius: 4px;
  padding: 6px 12px;
  margin: 4px 8px;
  color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 1rem;
}

.btn:not(.btn--close){
  box-shadow: 6px 6px 3px rgba(0, 0, 0, 0.1);
}

.btn--std{
  background: #2196F3;
  background: var(--primaryBlue);
}
.btn--std:hover{
  background: #1d7cca;
  background: var(--primaryActBlue);
}
.btn--std:active{
  background: #1d7cca;
  background: var(--primaryActBlue);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.btn--submit{
